import { Component,OnInit,ViewChild } from '@angular/core';
import {
  Facebook,
  FacebookLoginResponse
}
  from '@ionic-native/facebook/ngx';
  import { NativeStorage } from '@ionic-native/native-storage/ngx';
  import {Router, ActivatedRoute, Params} from '@angular/router';
  import { IonInfiniteScroll } from '@ionic/angular';
  import {MascotaService} from '../services/mascota.service'
  import { ToastController } from '@ionic/angular';
  import {GLOBAL} from '../services/global';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss'],
  providers:[Facebook,MascotaService]
})
export class Tab1Page implements OnInit {
  public nm;
  public apel;
  public im;
  public token;
  public status;
  public items =[];
  public status2;

  public pages;
  public itemsPerPage;
  public total;
  public mascotas=[];
  public page;
  public event:any;
  public url;
  //@ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;

  constructor(public toastController: ToastController,private _mascotaService:MascotaService, private fb: Facebook,private nativeStorage: NativeStorage,
		private _route:ActivatedRoute,
    private _router:Router) {
      this.page = 1;
      this.status = false;
      this.url = GLOBAL.url;

    }

  doRefresh(event) {

    setTimeout(() => {
      this.page = 1;
      this.obtMascotas(this.page)
      event.target.complete();

    }, 500);
  }
  doFbLogout(){
		this.fb.logout()
		.then(res =>{
			//user logged out so we will remove him from the NativeStorage
			this.nativeStorage.remove('facebook_user');
      this._router.navigate(['login']);
		}, error =>{
			console.log(error);
		});
  }
  
  ngOnInit(){
    this.nativeStorage.getItem('identity')
    .then(
    data => {
      
      this.nm = data.id;
      this.apel = data.apellidos;
      this.im = data.image;
    
    },
    error => console.log(<any>error)
  );
  this.nativeStorage.getItem('token')
  .then(
  data => {
    
    this.token = data.token;
    
  
  },
  error => console.log(<any>error)
);
this.obtMascotas(this.page)
  }
 
  async presentToast(txt) {
    const toast = await this.toastController.create({
      message: txt,
      duration: 2000,
      position: 'top'
    });
    toast.present();
    
  }
  loadData(event) {
    this.status = false;

    setTimeout(() => {

      if(this.mascotas.length != this.total){
        this.viewMore()
      }else{
        this.status = true;
      }
      
      event.target.complete();

      // App logic to determine if all data is loaded
      // and disable the infinite scroll
      /*if (data.length == 1000) {
        event.target.disabled = true;
      }*/
    }, 500);
  }
  async obtMascotas(page,adding=false){
    
    await this._mascotaService.obtMascotass(page).subscribe(
      response=>{
        
        if(response.mascotas && response.n == '1'){
          //this.fotos = response.fot;
          this.total = response.total;
          this.pages = response.pages;
          this.itemsPerPage = response.itemsPerPage;
          console.log(this.total)
         /* if(page > this.pages){
            this._router.navigate[('/login')]
          }*/
         if(!adding){

          this.mascotas = response.mascotas;

         }else{
           var arrayA = this.mascotas;
           var arrayB = response.mascotas;
           this.mascotas = arrayA.concat(arrayB)
         }

        }else{
          
          this.presentToast('Intentalo nuevamente')

        }
      },
      error=>{
        var errorMessage = <any>error;
        console.log(errorMessage)
        this.presentToast('Intentalo nuevamente')

        if(errorMessage != null && error.error.n == '2'){
          
        }else if(errorMessage != null && error.error.n == '3'){
          
        }else{
          
        }
      }
    )

  }
  public noMore = false;
  viewMore(){
    if(this.mascotas.length == this.total){
      this.noMore = true;
    }else{
      this.page += 1;
    }

    this.obtMascotas(this.page,true)
  }

  perfilMascota(id){
    this._router.navigate(['perfil-mascota',id]);       
  }
 /* toggleInfiniteScroll() {
    this.infiniteScroll.disabled = !this.infiniteScroll.disabled;
  }*/

}
